import Point from '@arcgis/core/geometry/Point';
import SpatialReference from '@arcgis/core/geometry/SpatialReference';
import Graphic from '@arcgis/core/Graphic';
import esriRequest from '@arcgis/core/request';
import PictureMarkerSymbol from '@arcgis/core/symbols/PictureMarkerSymbol';

export default class TextUtils {

    static txtToFeatures(url: string, spatialReference: SpatialReference): Promise<Graphic[]> {
        return new Promise((resolve) => {
            esriRequest(url, {
                responseType: 'text',
            }).then((r) => {
                const graphics: Graphic[] = [];
                r.data.split('\n').forEach((row: string, index: number) => {
                    if (index) { // la première ligne contient le nom des champs
                        const parts = row.split('\t');
                        if (parts.length >= 5) {
                            const coords = parts[0].split(',');
                            const geometry = new Point({ x: parseFloat(coords[1]), y: parseFloat(coords[0]), spatialReference });
                            const popupTemplate = {
                                title: parts[1],
                                content: parts[2],
                            };
                            const symbolSize = parts[4].split(',');
                            const symbol = new PictureMarkerSymbol({
                                url: parts[3],
                                width: `${symbolSize[0]}px`,
                                height: `${symbolSize[1]}px`,
                            });
                            graphics.push(new Graphic({
                                geometry,
                                symbol,
                                popupTemplate,
                            }));
                        }
                    }
                });
                resolve(graphics);
            });
        });
    }
}
